import React from "react"
import { Container, Button, Col, Row } from "react-bootstrap"
import { StaticImage } from "gatsby-plugin-image"
import "./Section2.scss"

export default function Section2() {
  return (
    <Container fluid className="firstcont py-xl-6 section-2-bg">
      <Container fluid="xl" className="py-xl-6">
        <Row>
          <Col
            lg={{ order: "last" }}
            md={{ order: "first" }}
            className="pl-xl-4"
          >
            <h1 className="h1-header text-center mb-5">
              Pave Credit is the easy way to borrow money.
            </h1>
          </Col>
        </Row>
        <Row className="justify-content-center">
          <Col lg={9} className="text-center">
            <StaticImage
              src="../../../../images/process.png"
              alt="Jump Mobile App"
              className="d-none d-sm-block"
            />
            <StaticImage
              src="../../../../images/process-mobile.png"
              alt="Jump Mobile App"
              className="d-sm-none"
            />
          </Col>
        </Row>
        <Row className="pt-5">
          <Col className="text-center">
            <Button variant="seconday" className="w-25 p-3 ">
              Apply Now
            </Button>
          </Col>
        </Row>
      </Container>
    </Container>
  )
}
