import React, { Component } from "react"
import Slider from "react-slick"
import "../../../../node_modules/slick-carousel/slick/slick.css"
import "../../../../node_modules/slick-carousel/slick/slick-theme.css"
import "./PreApplicationSlider.scss"
import { Container, Button, Col, Row, Form, Dropdown } from "react-bootstrap"
import SimpleMaskMoney from "../../../../node_modules/simple-mask-money/lib/simple-mask-money.js"

export default class SimpleSlider2 extends Component {
  constructor(props) {
    super(props)
    this.next = this.next.bind(this)
    this.previous = this.previous.bind(this)
  }
  componentDidMount() {
    const args = {
      afterFormat(e) {
        console.log("afterFormat", e)
      },
      allowNegative: false,
      beforeFormat(e) {
        console.log("beforeFormat", e)
      },
      negativeSignAfter: false,
      prefix: "$",
      suffix: "",
      fixed: true,
      fractionDigits: 2,
      decimalSeparator: ".",
      thousandsSeparator: ",",
      cursor: "move",
    }
    const input = SimpleMaskMoney.setMask("#amountInput", args)

    // This method return value of your input in format number to save in your database
    input.formatToNumber()
  }
  next() {
    this.slider.slickNext()
  }
  previous() {
    this.slider.slickPrev()
  }
  CloseSlider() {
    window.onscroll = function () {}
    document.querySelector("div.pre-application-slider").style.display = "none"
  }
  updateDropDown(e) {
    document.getElementById("dropdown-basic").innerHTML = e.substr(1)
  }
  render() {
    const settings = {
      dots: false,
      infinite: false,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      swipeToSlide: false,
      swipe: false,
      arrows: false,
    }

    // configuration

    return (
      <div className=" pre-application-slider ">
        <Slider ref={c => (this.slider = c)} {...settings}>
          <Container className="">
            <h1 className="h1-header pt-4  text-white text-center">
              What state are you in?
            </h1>
            <Row className=" mt-5 justify-content-end">
              <Col className="text-sm-left mb-5" xs={12} lg={3}>
                <div className="custom-select-state mx-auto mr-sm-0">
                  <select name="cars" id="mainStateDropdown">
                    <option value="">State</option>
                    <option value="AL">Alabana</option>
                    <option value="TN">Tennessee</option>
                    <option value="KS">Kansas</option>
                  </select>
                </div>
              </Col>

              <Col className="text-sm-left" lg={6} xs={12}>
                <Button
                  onClick={this.next}
                  variant="primary"
                  className="w-25 py-2"
                >
                  Next
                </Button>
              </Col>
            </Row>
            <Row>
              <Col lg={3} className="slider-close-btn mx-auto">
                {" "}
                <Button
                  onClick={this.CloseSlider}
                  variant="primary"
                  className="w-50 py-2 prev-btn"
                >
                  Close
                </Button>
              </Col>
            </Row>
          </Container>
          <Container className="">
            <h1 className="h1-header pt-4  text-white text-center">
              How much would you like to borrow?
            </h1>
            <Row className="text-center mt-5">
              <Col className="text-right">
                <Button
                  onClick={this.previous}
                  variant="primary"
                  className="w-50 py-2 prev-btn"
                >
                  Prev
                </Button>
              </Col>
              <Col
                xs={{ order: "first", span: 12 }}
                className="text-right px-4 mb-5"
              >
                <Col xs={12} lg={6} className="mx-auto">
                  <Form.Control
                    type="text"
                    className="w-100 m-auto text-center"
                    placeholder="$"
                    id="amountInput"
                    defaultValue="1000"
                  />
                </Col>
              </Col>

              <Col className="text-left">
                <Button
                  onClick={this.next}
                  variant="primary"
                  className="w-50 py-2"
                >
                  Next
                </Button>
              </Col>
            </Row>
            <Row>
              <Col lg={3} className="slider-close-btn mx-auto">
                {" "}
                <Button
                  onClick={this.CloseSlider}
                  variant="primary"
                  className="w-100 w-sm-50 py-2 prev-btn"
                >
                  Close
                </Button>
              </Col>
            </Row>
          </Container>
          <Container className="mb-5">
            <h1 className="h1-header pt-4  text-white text-center">
              What do you need it for?
            </h1>
            <Row className="text-center mt-5">
              <Col className="text-right m-0 p-0" xs={4} lg={5}>
                <Col lg={4} className="ml-auto">
                  <Button
                    onClick={this.previous}
                    variant="primary"
                    className="w-100 py-2 prev-btn"
                  >
                    Prev
                  </Button>
                </Col>
              </Col>
              <Col
                xs={{ order: "first", span: 12 }}
                className="text-right mb-5"
              >
                <Col lg={5} className="mx-auto">
                  <Dropdown onSelect={this.updateDropDown}>
                    <Dropdown.Toggle
                      defaultValue=""
                      variant="success"
                      id="dropdown-basic"
                    >
                      Select one
                    </Dropdown.Toggle>

                    <Dropdown.Menu>
                      <Dropdown.Item href="#Car Repair">
                        Car Repair
                      </Dropdown.Item>
                      <Dropdown.Item href="#Medical Bills">
                        Medical Bills
                      </Dropdown.Item>
                      <Dropdown.Item href="#Home Improvements">
                        Home Improvements
                      </Dropdown.Item>
                      <Dropdown.Item href="#Vacation">Vacation</Dropdown.Item>
                      <Dropdown.Item href="#Other Emergencies">
                        Other Emergencies
                      </Dropdown.Item>
                      <Dropdown.Item href="#Others...">Others...</Dropdown.Item>
                    </Dropdown.Menu>
                  </Dropdown>
                </Col>
              </Col>

              <Col className="text-sm-left" xs={8} lg={6}>
                <Col lg={6} className="ml-0">
                  <Button
                    onClick={this.next}
                    variant="primary"
                    className="w-100 w-sm-50 py-2"
                  >
                    Start Application
                  </Button>
                </Col>
              </Col>
            </Row>
            <Row>
              <Col lg={3} className="slider-close-btn mx-auto">
                {" "}
                <Button
                  onClick={this.CloseSlider}
                  variant="primary"
                  className="w-50 py-2 prev-btn"
                >
                  Close
                </Button>
              </Col>
            </Row>
          </Container>
        </Slider>{" "}
        {/*
        <div style={{ textAlign: "center" }}>
          <button className="button" onClick={this.previous}>
            Previous
          </button>
          <button className="button" onClick={this.next}>
            Next
          </button>
        </div> */}
      </div>
    )
  }
}
