import React, { useState } from "react"
import { Container, Button, Col, Row } from "react-bootstrap"
import Typewriter from "typewriter-effect"
import PreApplicationSlider2 from "../../../../components/Extras/PreApplicationSlider/PreApplicationSlider2"
import "./Section1.scss"

export default function Section1() {
  const displaySliderForm = () => {
    document.querySelector("div.pre-application-slider").style.display = "block"
    document.body.style.overflow = "hidden"
    document.querySelector("html").scrollTop = window.scrollY

    let scrollTop = window.pageYOffset || document.documentElement.scrollTop
    let scrollLeft = window.pageXOffset || document.documentElement.scrollLeft
    // if any scroll is attempted, set this to the previous value
    window.onscroll = function () {
      window.scrollTo(scrollLeft, scrollTop)
    }
  }
  return (
    <Container fluid className="top-home home-page-1 ">
      <PreApplicationSlider2 />
      <Container fluid className="home-slider">
        <Container
          fluid="xl"
          className="home-slider-container d-flex flex-column"
        >
          <Row className="align-self-start mt-5 pt-5 my-auto mx-auto ">
            <Col xs="12" className="typewriter-col">
              <span className="Typewriter__wrapper pr-1">
                No more rocky roads
              </span>
              {/*<Typewriter
                options={{
                  strings: [
                    " unexpected emergencies",
                    " utilities",
                    " medical bills",
                    "car repairs",
                    "life...",
                  ],
                  autoStart: true,
                  loop: true,
                }}
              />*/}
            </Col>
            <Col xs="12 mt-3">
              <Button
                variant="primary"
                onClick={() => {
                  displaySliderForm()
                }}
                className="w-50 p-3"
              >
                Apply now
              </Button>
            </Col>
          </Row>
        </Container>
      </Container>
    </Container>
  )
}
